const state = {
    list: [
        { id: 1, name: ' '},
        { id: 2, name: ' '},
        { id: 4, name: ' '},
        { id: 5, name: '     '},
        { id: 6, name: ' '},
    ],
    current: [],
}

export default {
    actions: {},
    mutations: {
        setFilterList(state, types) {
            state.list = types
        },
        setCurrentFilters(state, filters) {
            state.current = filters
        }
    },
    state,
    getters: {
        filters: (state) => {
            return {
                list: state.list,
                current: state.current
            }
        },
    }
}
